* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}


nav ul {
    display: flex;
    flex-grow: 1;
    justify-content: space-around;
    list-style: none;
    margin: 0 2px;
    padding: 0;
  }

nav {
  background:rgb(124, 128, 131);
}

.navbar-brand {
  font-family: 'ZCOOL XiaoWei', serif;
  color: white;
  font-size: 2em;
}

a {
  font-family: 'ZCOOL XiaoWei', serif;
  color: gray;
  cursor: pointer;
}

.jumbotron {
    background-image: url("http://www.theonering.net/torwp/wp-content/uploads/2013/07/lord-of-the-rings-fellowship-of-the-ring-the-silhouettes.jpg");
    resize: both;
    background-size: 100% 100%, cover;
    height: 700px;
    color: gray;
}

.display-4, p {
    font-family: 'ZCOOL XiaoWei', serif;
}
.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  margin-top: -35px;
  
}

footer {
    background:rgb(124, 128, 131);
    color: white;
}
.card {
    background: #fff;
    border-radius: 2px;
    height: 260px;
    margin: 1rem;
    position: relative;
    width: 260px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    cursor: pointer;
  }
  
  .card > .img-container {
    height: 60%;
    overflow: hidden;
    text-align: center;
    background:rgb(124, 128, 131);
  }
  .card > .img-container > img {
    width: 60%
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 14px;
  }
  
  .card > .content > ul {
    list-style-type: none;
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
  }
  
  .card > .remove {
    color: #ff1744;
    font-size: 48px;
    position: absolute;
    top: -20px;
    right: 5px;
    cursor: pointer;
  }
  
  .card > .remove:hover {
    color: #d50000;
  }
  
  .card:hover {
      -webkit-transform: scale(1.1);
              transform: scale(1.1); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
  }
.reset-btn {
    font-family: 'ZCOOL XiaoWei', serif;
    margin: 0 auto;
    margin-top: 15px;
}
body {
  padding-top: 55px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}
